/**
 * @file 页面不存在 router
 * @author linliu21
 */
import Index from '@/pages/404/index.vue'

export default {
  routes: [
    {
      path: '/',
      redirect: '/404'
    },
    {
      path: '/404',
      name: '404',
      component: Index
    }
  ]
}



